import { Component, ElementRef, EventEmitter, OnDestroy, OnInit, ViewChild, ViewEncapsulation, AfterViewInit } from '@angular/core';
import jsQR from "jsqr";
import { AsyncSubject, BehaviorSubject } from 'rxjs';
import { CONFIG_DEFAULT, MEDIA_STREAM_DEFAULT } from './ngx-scanner-qrcode-single.default';
import { AS_COMPLETE, BLOB_TO_FILE, CANVAS_TO_BLOB, DRAW_RESULT_APPEND_CHILD, FILES_TO_SCAN, OVERRIDES, PLAY_AUDIO, PROP_EXISTS, REMOVE_CANVAS, VIBRATE } from './ngx-scanner-qrcode-single.helper';
import { ScannerQRCodeConfig, ScannerQRCodeDevice, ScannerQRCodeResult, ScannerQRCodeSelectedFiles } from './ngx-scanner-qrcode-single.options';

@Component({
  selector: 'ngx-scanner-qrcode-single',
  template: `<div #resultsPanel class="origin-overlay"></div><canvas #canvas class="origin-canvas"></canvas><video #video playsinline class="origin-video"></video>`,
  styleUrls: ['./ngx-scanner-qrcode-single.component.scss'],
  host: { 'class': 'ngx-scanner-qrcode-single' },
  exportAs: 'scanner',
  inputs: ['src', 'fps', 'vibrate', 'isBeep', 'config', 'constraints', 'canvasStyles'],
  outputs: ['event'],
  queries: {
    video: new ViewChild('video'),
    canvas: new ViewChild('canvas'),
    resultsPanel: new ViewChild('resultsPanel')
  },
  encapsulation: ViewEncapsulation.None
})
export class NgxScannerQrcodeSingleComponent implements OnInit, OnDestroy, AfterViewInit {

  /**
   * Element
   * playsinline required to tell iOS safari we don't want fullscreen
   */
  public video!: ElementRef<HTMLVideoElement>;
  public canvas!: ElementRef<HTMLCanvasElement>;
  public resultsPanel!: ElementRef<HTMLDivElement>;

  /**
   * EventEmitter
   */
  public event = new EventEmitter<ScannerQRCodeResult>();

  /**
   * Input
   */
  public src: string | undefined = CONFIG_DEFAULT.src;
  public fps: number | undefined = CONFIG_DEFAULT.fps;
  public vibrate: number | undefined = CONFIG_DEFAULT.vibrate;
  public isBeep: boolean | undefined = CONFIG_DEFAULT.isBeep;
  public config: ScannerQRCodeConfig = CONFIG_DEFAULT;
  public constraints: MediaStreamConstraints | any = CONFIG_DEFAULT.constraints;
  public canvasStyles: CanvasRenderingContext2D | any = CONFIG_DEFAULT.canvasStyles;

  /**
   * Export
  */
  public isStart: boolean = false;
  public isPause: boolean = false;
  public isLoading: boolean = false;
  public isTorch: boolean = false;
  public data = new BehaviorSubject<ScannerQRCodeResult>(null);
  public devices = new BehaviorSubject<ScannerQRCodeDevice[]>([]);
  public deviceIndexActive: number = 0;

  /**
   * Private
  */
  private rAF_ID: any;
  private dataForResize!: ScannerQRCodeResult;
  private ready = new AsyncSubject<boolean>();

  private STATUS = {
    startON: () => this.isStart = true,
    pauseON: () => this.isPause = true,
    loadingON: () => this.isLoading = true,
    startOFF: () => this.isStart = false,
    pauseOFF: () => this.isPause = false,
    loadingOFF: () => this.isLoading = false,
    torchOFF: () => this.isTorch = false,
  }

  constructor() { }

  ngOnInit(): void {
    this.overrideConfig();
    if (this.src) {
      this.loadImage(this.src);
    }
    this.resize();
  }
  
  ngAfterViewInit(): void {
    setTimeout(() => AS_COMPLETE(this.ready, true), 1000);
  }

  /**
   * start
   * @param playDeviceCustom 
   * @return AsyncSubject
   */
  public start(playDeviceCustom?: Function): AsyncSubject<any> {
    const as = new AsyncSubject<any>();
    if (this.isStart) {
      // Reject
      AS_COMPLETE(as, false);
    } else {
      // fix safari
      this.safariWebRTC(as, playDeviceCustom);
    }
    return as;
  }

  /**
   * stop
   * @return AsyncSubject
   */
  public stop(): AsyncSubject<any> {
    this.STATUS.pauseOFF();
    this.STATUS.startOFF();
    this.STATUS.torchOFF();
    this.STATUS.loadingOFF();
    const as = new AsyncSubject<any>();
    try {
      clearInterval(this.rAF_ID);
      (this.video.nativeElement.srcObject as MediaStream).getTracks().forEach((track: MediaStreamTrack) => {
        track.stop();
        AS_COMPLETE(as, true);
      });
      REMOVE_CANVAS(this.resultsPanel.nativeElement);
    } catch (error) {
      AS_COMPLETE(as, false, error as any);
    }
    return as;
  }

  /**
   * play
   * @return AsyncSubject
   */
  public play(): AsyncSubject<any> {
    const as = new AsyncSubject<any>();
    if (this.isPause) {
      this.video.nativeElement.play();
      this.STATUS.pauseOFF();
      this.requestAnimationFrame(90);
      AS_COMPLETE(as, true);
    } else {
      AS_COMPLETE(as, false);
    }
    return as;
  }

  /**
   * pause
   * @return AsyncSubject 
   */
  public pause(): AsyncSubject<any> {
    const as = new AsyncSubject<any>();
    if (this.isStart) {
      clearInterval(this.rAF_ID);
      this.video.nativeElement.pause();
      this.STATUS.pauseON();
      AS_COMPLETE(as, true);
    } else {
      AS_COMPLETE(as, false);
    }
    return as;
  }

  /**
   * playDevice
   * @param deviceId 
   * @param as 
   * @return AsyncSubject 
   */
  public playDevice(deviceId: string, as: AsyncSubject<any> = new AsyncSubject<any>()): AsyncSubject<any> {
    const existDeviceId = this.isStart ? this.getConstraints().deviceId !== deviceId : true;
    switch (true) {
      case deviceId === 'null' || deviceId === 'undefined' || !deviceId:
        stop();
        this.stop();
        AS_COMPLETE(as, false);
        break;
      case deviceId && existDeviceId:
        stop();
        this.stop();
        // Loading on
        this.STATUS.loadingON();
        this.deviceIndexActive = this.devices.value.findIndex((f: ScannerQRCodeDevice) => f.deviceId === deviceId);
        const constraints = { ...this.constraints, audio: false, video: { deviceId: deviceId, ...this.constraints.video } };
        // MediaStream
        navigator.mediaDevices.getUserMedia(constraints).then((stream: MediaStream) => {
          this.video.nativeElement.srcObject = stream;
          this.video.nativeElement.onloadedmetadata = () => {
            this.video.nativeElement.play();
            this.requestAnimationFrame();
            AS_COMPLETE(as, true);
            this.STATUS.startON();
            this.STATUS.loadingOFF();
          }
        }).catch((error: any) => {
          this.eventEmit(false);
          AS_COMPLETE(as, false, error);
          this.STATUS.startOFF();
          this.STATUS.loadingOFF();
        });
        break;
      default:
        AS_COMPLETE(as, false);
        this.STATUS.loadingOFF();
        break;
    }
    return as;
  }

  /**
   * loadImage
   * @param src 
   * @return AsyncSubject
   */
  public loadImage(src: string): AsyncSubject<any> {
    const as = new AsyncSubject<any>();
    // Loading on
    this.STATUS.startOFF();
    this.STATUS.loadingON();
    // Set the src of this Image object.
    const image = new Image();
    // Setting cross origin value to anonymous
    image.setAttribute('crossOrigin', 'anonymous');
    // When our image has loaded.
    image.onload = () => {
      this.drawImage(image, (flag: boolean) => {
        AS_COMPLETE(as, flag);
        this.STATUS.startOFF();
        this.STATUS.loadingOFF();
      });
    };
    // Set src
    image.src = src;
    return as;
  }

  /**
   * torcher
   * @return AsyncSubject
   */
  public torcher(): AsyncSubject<any> {
    const as = this.applyConstraints({ advanced: [{ torch: this.isTorch }] });
    as.subscribe(() => false, () => this.isTorch = !this.isTorch);
    return as;
  }

  /**
   * applyConstraints
   * @param constraints 
   * @return AsyncSubject
   */
  public applyConstraints(constraints: MediaTrackConstraintSet | MediaTrackConstraints | any): AsyncSubject<any> {
    const as = new AsyncSubject<any>();
    const stream = this.video.nativeElement.srcObject as MediaStream;
    const videoTrack = stream.getVideoTracks()[0] as MediaStreamTrack;
    const imageCapture = new (window as any).ImageCapture(videoTrack);
    imageCapture.getPhotoCapabilities().then(async () => {
      await videoTrack.applyConstraints(constraints);
      AS_COMPLETE(as, true);
    }).catch((error: any) => {
      switch (error && error.name) {
        case 'NotFoundError':
        case 'DevicesNotFoundError':
          AS_COMPLETE(as, false, 'Required track is missing' as any);
          break;
        case 'NotReadableError':
        case 'TrackStartError':
          AS_COMPLETE(as, false, 'Webcam or mic are already in use' as any);
          break;
        case 'OverconstrainedError':
        case 'ConstraintNotSatisfiedError':
          AS_COMPLETE(as, false, 'Constraints can not be satisfied by avb. devices' as any);
          break;
        case 'NotAllowedError':
        case 'PermissionDeniedError':
          AS_COMPLETE(as, false, 'Permission denied in browser' as any);
          break;
        case 'TypeError':
          AS_COMPLETE(as, false, 'Empty constraints object' as any);
          break;
        default:
          AS_COMPLETE(as, false, error as any);
          break;
      }
    });
    return as;
  };

  /**
   * getConstraints
   * @returns 
   */
  public getConstraints(): MediaTrackConstraintSet | any {
    const stream = this.video.nativeElement.srcObject as MediaStream;
    const videoTrack = stream && stream.getVideoTracks()[0] as MediaStreamTrack;
    return videoTrack && videoTrack.getConstraints() as any;
  }

  /**
   * download
   * @param fileName 
   * @return AsyncSubject
   */
  public download(fileName: string = `ngx-scanner-qrcode-single-${Date.now()}.png`): AsyncSubject<ScannerQRCodeSelectedFiles[]> {
    const as = new AsyncSubject<any>();
    const run = async () => {
      const blob = await CANVAS_TO_BLOB(this.canvas.nativeElement);
      const file = BLOB_TO_FILE(blob, fileName);
      FILES_TO_SCAN([file], this.config, as).subscribe((res: ScannerQRCodeSelectedFiles[]) => {
        res.forEach((item: ScannerQRCodeSelectedFiles) => {
          const link = document.createElement('a');
          link.href = item.url;
          link.download = item.name;
          link.click();
          link.remove()
        });
      });
    }
    run();
    return as;
  }

  /**
   * window: resize
   * Draw again!
   */
  private resize(): void {
    window.addEventListener("resize", () => {
      if (this.dataForResize) {
        DRAW_RESULT_APPEND_CHILD(this.dataForResize as any, this.canvas.nativeElement, this.resultsPanel.nativeElement, this.canvasStyles);
        this.video.nativeElement.style.height = this.canvas.nativeElement.offsetHeight + 'px';
      }
    });
  }

  /**
   * Override config
   * @return void
   */
  private overrideConfig(): void {
    if (PROP_EXISTS(this.config, 'src')) this.src = this.config.src;
    if (PROP_EXISTS(this.config, 'fps')) this.fps = this.config.fps;
    if (PROP_EXISTS(this.config, 'vibrate')) this.vibrate = this.config.vibrate;
    if (PROP_EXISTS(this.config, 'isBeep')) this.isBeep = this.config.isBeep;
    if (PROP_EXISTS(this.config, 'constraints')) this.constraints = OVERRIDES('constraints', this.config, MEDIA_STREAM_DEFAULT);
    if (PROP_EXISTS(this.config, 'canvasStyles')) this.canvasStyles = this.config.canvasStyles;
  }

  /**
   * safariWebRTC
   * Fix issue on safari
   * https://webrtchacks.com/guide-to-safari-webrtc
   * @param as 
   * @param playDeviceCustom 
   */
  private safariWebRTC(as: AsyncSubject<any>, playDeviceCustom?: Function): void {
    // Loading on
    this.STATUS.startOFF();
    this.STATUS.loadingON();
    navigator.mediaDevices.getUserMedia(this.constraints).then((stream: MediaStream) => {
      stream.getTracks().forEach(track => track.stop());
      this.loadAllDevices(as, playDeviceCustom);
    }).catch((error: any) => {
      AS_COMPLETE(as, false, error);
      this.STATUS.startOFF();
      this.STATUS.loadingOFF();
    });
  }

  /**
   * loadAllDevices
   * @param as 
   * @param playDeviceCustom 
   */
  private loadAllDevices(as: AsyncSubject<any>, playDeviceCustom?: Function): void {
    navigator.mediaDevices.enumerateDevices().then(devices => {
      let cameraDevices: ScannerQRCodeDevice[] = devices.filter(f => f.kind == 'videoinput');
      this.devices.next(cameraDevices);
      if (cameraDevices.length > 0) {
        AS_COMPLETE(as, cameraDevices);
        playDeviceCustom ? playDeviceCustom(cameraDevices) : this.playDevice(cameraDevices[0].deviceId);
      } else {
        AS_COMPLETE(as, false, 'No camera detected.' as any);
        this.STATUS.startOFF();
        this.STATUS.loadingOFF();
      }
    }).catch((error: any) => {
      AS_COMPLETE(as, false, error);
      this.STATUS.startOFF();
      this.STATUS.loadingOFF();
    });
  }

  /**
   * drawImage
   * @param element 
   * @param callback 
   */
  private async drawImage(element: HTMLImageElement | HTMLVideoElement, callback: Function = () => { }): Promise<void> {
    // Get the canvas element by using the getElementById method.
    const canvas = this.canvas.nativeElement;
    // Get a 2D drawing context for the canvas.
    const ctx = canvas.getContext('2d', { willReadFrequently: true }) as CanvasRenderingContext2D;
    // HTMLImageElement size
    if (element instanceof HTMLImageElement) {
      canvas.width = element.naturalWidth;
      canvas.height = element.naturalHeight;
      element.style.visibility = '';
      this.video.nativeElement.style.visibility = 'hidden';
      this.video.nativeElement.style.height = canvas.offsetHeight + 'px';
    }
    // HTMLVideoElement size
    if (element instanceof HTMLVideoElement) {
      canvas.width = element.videoWidth;
      canvas.height = element.videoHeight;
      element.style.visibility = '';
      this.canvas.nativeElement.style.visibility = 'hidden';
    }
    ctx.clearRect(0, 0, canvas.width, canvas.height)
    // Draw image
    ctx.drawImage(element, 0, 0, canvas.width, canvas.height);
    // Data image
    const imageData = ctx.getImageData(0, 0, canvas.width, canvas.height);
    // Draw frame
    const code = jsQR(imageData.data, imageData.width, imageData.height, {
      inversionAttempts: "dontInvert",
    }) as ScannerQRCodeResult;
    if (code && code.data !== '') {
      // Overlay
      DRAW_RESULT_APPEND_CHILD(code, this.canvas.nativeElement, this.resultsPanel.nativeElement, this.canvasStyles);

      // To blob and emit data
      const EMIT_DATA = () => {
        this.eventEmit(code);
        this.dataForResize = code;
      };

      // HTMLImageElement
      if (element instanceof HTMLImageElement) {
        callback(true);
        EMIT_DATA();
        VIBRATE(this.vibrate);
        PLAY_AUDIO(this.isBeep);
      }
      // HTMLVideoElement
      if (element instanceof HTMLVideoElement) {
        EMIT_DATA();
        VIBRATE(this.vibrate);
        PLAY_AUDIO(this.isBeep);
      }
    } else {
      callback(false);
      REMOVE_CANVAS(this.resultsPanel.nativeElement);
      this.dataForResize = code;
    }
  }

  /**
   * eventEmit
   * @param response 
   */
  private eventEmit(response: any = false): void {
    (response !== false) && this.data.next(response || { data: null });
    (response !== false) && this.event.emit(response || { data: null });
  }

  /**
   * Single-thread
   * Loop Recording on Camera
   * Must be destroy request 
   * Not using: requestAnimationFrame
   * @param delay
   */
  private requestAnimationFrame(delay: number = 0): void {
    this.rAF_ID = setInterval(() => {
      if (this.video.nativeElement.readyState === this.video.nativeElement.HAVE_ENOUGH_DATA) {
        delay = 0;
        this.drawImage(this.video.nativeElement);
      }
    }, /*avoid cache mediaStream*/ delay || this.fps);
  }

  /**
   * Status of wasm
   * @return AsyncSubject
   */
  get isReady(): AsyncSubject<boolean> {
    return this.ready;
  }

  ngOnDestroy(): void {
    this.pause();
  }
}
