import { Injectable } from '@angular/core';
import { AsyncSubject } from 'rxjs';
import { AS_COMPLETE, COMPRESS_IMAGE, FILES_TO_SCAN } from './ngx-scanner-qrcode-single.helper';
import { ScannerQRCodeConfig, ScannerQRCodeSelectedFiles } from './ngx-scanner-qrcode-single.options';

@Injectable({
  providedIn: 'root'
})
export class NgxScannerQrcodeSingleService {

  /**
   * loadFiles
   * @param files 
   * @param quality 
   * @param type 
   * @returns 
   */
  public loadFiles(files: File[] = [], quality = 0.5, type = 'image/jpeg'): AsyncSubject<ScannerQRCodeSelectedFiles[]> {
    const as = new AsyncSubject<ScannerQRCodeSelectedFiles[]>();
    COMPRESS_IMAGE(files, quality, type).then((_files: any) => {
      Promise.all(Object.assign([], _files).map((m: File) => this.readAsDataURL(m))).then((img: any[]) => AS_COMPLETE(as, img)).catch((error: any) => AS_COMPLETE(as, null, error));
    }).catch(error => {
      AS_COMPLETE(as, null, error as any);
    });
    return as;
  }

  /**
   * loadFilesToScan
   * @param files 
   * @param config 
   * @param quality 
   * @param type 
   * @returns 
   */
  public loadFilesToScan(files: File[] = [], config: ScannerQRCodeConfig, quality = 0.5, type = 'image/jpeg'): AsyncSubject<ScannerQRCodeSelectedFiles[]> {
    const as = new AsyncSubject<ScannerQRCodeSelectedFiles[]>();
    COMPRESS_IMAGE(files, quality, type).then((_files: any) => {
      FILES_TO_SCAN(_files, config, as);
    }).catch(error => {
      AS_COMPLETE(as, null, error as any);
    });
    return as
  }

  /**
   * readAsDataURL
   * @param file 
   * @return Promise
   */
  private readAsDataURL(file: File): Promise<ScannerQRCodeSelectedFiles> {
    /** drawImage **/
    return new Promise((resolve, reject) => {
      const fileReader = new FileReader();
      fileReader.onload = () => {
        const objectFile = {
          name: file.name,
          file: file,
          url: URL.createObjectURL(file)
        };
        resolve(objectFile);
      }
      fileReader.onerror = (error: any) => reject(error);
      fileReader.readAsDataURL(file);
    })
  }
}